.menu {
  font-size: 1.2em;
  padding: 0;
  color: $main-color;
  display: flex;
  justify-content: flex-end;
  text-transform: uppercase;

  li {
    padding: 0 15px 5px 15px;
    margin: 0 15px;
    cursor: pointer;
  }

  &--item {
    &-selected {
      border-bottom: 3px solid $main-color;
    }
  }
}