@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,600,700);
.cv-data {
  font-size: 0.85em;
  display: flex;
  margin-bottom: 5px; }
  .cv-data:last-child {
    margin-bottom: 0px; }
  .cv-data .divider {
    border-left: 1px solid #969696; }
  .cv-data-years {
    flex: 20% 1;
    text-align: right;
    padding-right: 8px;
    font-weight: 400; }
  .cv-data-info {
    flex: 85% 1;
    padding-left: 8px; }

@media (max-width: 769px) {
  .cv-data-years div span {
    display: block; } }

@media (min-width: 415px) {
  .small-phone {
    display: none; } }

@media (max-width: 415px) {
  .cv-data {
    flex-direction: column; }
    .cv-data .divider {
      border-left: inherit; }
    .cv-data-years {
      text-align: inherit;
      font-weight: 400; }
      .cv-data-years div {
        display: inline; }
        .cv-data-years div:last-child::before {
          content: ' - '; }
        .cv-data-years div span {
          display: inline; }
    .cv-data-info {
      padding-left: inherit; }
  .no-small-phone {
