@media (max-width: 769px) {
  .languages .language-name {
    flex: 30% 1; }
  .languages .language-title {
    flex: 30% 1; }
  .languages .language-level {
    flex: 30% 1; } }

@media (max-width: 415px) {
  .languages .language-name {
    flex: 25% 1; }
  .languages .language-title {
    flex: 25% 1; }
  .languages .language-level {
    flex: 50% 1; } }

.education-data-title--name {
  font-weight: 400; }

.education-university {
  font-size: 0.9em;
  font-style: italic; }

.education-major-subjects, .education-thesis {
  font-size: 0.8em;
  text-align: justify; }
  .education-major-subjects span, .education-thesis span {
    font-style: italic; }
  .education-major-subjects ul, .education-thesis ul {
    -webkit-padding-start: 5px;
            padding-inline-start: 5px; }

.section--header {
  color: #24717c;
  font-size: 1.5em;
  padding-bottom: 5px;
  border-bottom: 1px solid #969696;
  margin-bottom: 12px; }

.section--title {
  font-weight: 300;
  margin-left: 10px;
  text-transform: uppercase;
  display: inline-block;
  -webkit-margin-after: 0px;
          margin-block-end: 0px;
  -webkit-margin-before: 0px;
          margin-block-start: 0px; }
  .section--title::first-letter {
    font-size: 1.4em; }

@media (min-width: 569px) {
  .section {
    padding: 0 25px; } }

.skills .skill-line {
  font-size: 0.85em; }

.skills .skill-title {
  font-weight: 400; }

.skills .skill-list ul {
  padding: 0; }
  .skills .skill-list ul:before {
    content: "- "; }
  .skills .skill-list ul li strong {
    font-weight: 400; }
  .skills .skill-list ul li:after {
    content: ", "; }
  .skills .skill-list ul li:last-child:after {
    content: ""; }

@media (min-width: 415px) {
  .skills .skill-line {
    display: flex; }
  .skills .skill-title {
    flex: 30% 1;
    padding-right: 7px;
    text-align: right; }
  .skills .skill-list {
    flex: 70% 1; }
    .skills .skill-list:before {
      content: ""; }
    .skills .skill-list ul {
      padding: 0; }
      .skills .skill-list ul:before {
        content: ""; } }

@media (min-width: 769px) {
  .skills .skill-title {
    flex: 25% 1; } }

.header {
  margin: 40px 0 0 0;
  font-size: 0.75em;
  margin-bottom: 5px; }
  .header svg {
    color: #24717c; }
  .header-photo-container {
    text-align: center;
    margin-bottom: 20px; }
    .header-photo-container--picture {
      border-radius: 50%;
      height: 8em;
      width: 8em; }
  .header-info {
    order: 0; }
    .header-info p {
      -webkit-margin-after: 0px;
              margin-block-end: 0px;
      -webkit-margin-before: 0px;
              margin-block-start: 0px; }
    .header-info .header-fullname {
      text-align: center;
      font-size: 1.7em;
      color: #24717c; }
      .header-info .header-fullname p {
        display: inline-block;
        margin-top: 3px; }
        .header-info .header-fullname p:first-letter {
          font-size: 1.3em; }
      .header-info .header-fullname--name {
        font-weight: 300; }
      .header-info .header-fullname--surname {
        text-transform: uppercase;
        margin-left: 0.3em;
        font-weight: 400; }
    .header-info .header-position {
      text-align: center;
      color: #24717c;
      font-size: 1.3em;
      font-weight: 400;
      margin: 5px 0 3px 0; }
    .header-info .header-contact--link a {
