@media (min-width: 569px) {
  .header {
    display: flex;
    flex-direction: row;
    align-items: stretch;
    justify-content: space-between;
    margin-bottom: 20px; }
    .header-photo-container {
      order: 1; }
      .header-photo-container--picture {
        border-radius: 50%;
        height: 10em;
        width: 10em; }
    .header-info {
      order: 0; }
      .header-info p {
        display: inline-block;
        margin-top: 3px; }
      .header-info .add-space {
        margin-left: 10px; }
      .header-info .header-fullname {
        text-align: left; }
      .header-info .header-position {
        text-align: left; } }

.menu {
  font-size: 1.2em;
  padding: 0;
  color: #24717c;
  display: flex;
  justify-content: flex-end;
  text-transform: uppercase; }
  .menu li {
