.cv {
  font-size: 1rem;
  width: 100%;
  background: #fff;
  justify-content: flex-end;
  padding: 5px; }

@media (min-width: 769px) {
  .cv {
    margin: 40px 0;
    padding: 20px;
    width: 769px;
    box-shadow: 0px 0px 50px -10px #101010; }
    .cv .section-wrapper {
      display: flex; }
      .cv .section-wrapper > div {
