  color: black; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

ul {
  display: inline;
  list-style: none; }
  ul li {
    display: inline; }

.container {
  display: flex;
  justify-content: center; }

a.link {
  text-decoration: none;
  color: #24717c; }
  a.link:visited {
    color: #24717c; }

